C/C;;AAEA;IACI,8BAA8B;AAClC;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,cAAc;IACd,qCAAqC;IACrC,iCAAiC;IACjC,eAAe;AACnB","sourcesContent":["@import url(\"../fonts/Roboto-Font/stylesheet.css\");\n@import url(\"../fonts/alex-brush/stylesheet.css\");\n\n:root {\n    --main-color: #222831;\n    --secondary-color: #393E46;\n    --tertiary-color: #00ADB5;\n    --dark-tertiary-color: hsl(183, 100%, 25%);\n    --quaternary-color: #EEEEEE;\n    --logo-color: #FFC90E; \n    --pad-height: 8px;\n    --pad-width: 16px;\n    --pad-neg-height: calc(var(--pad-height) - (var(--pad-height) * 2));\n    --pad-neg-width: calc(var(--pad-width) - (var(--pad-width) * 2));\n    --mild-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);\n    --generic-padding: var(--pad-height) var(--pad-width);\n    --small-padding: calc(var(--pad-height) / 2) calc(var(--pad-width) / 2);\n    --softer-shadow: 5px 5px 10px rgba(0, 0, 0, 0.10);\n    --light-tertiary-color: hsl(183, 100%, 45%);\n}\n\nbody {\n    height: 100vh;\n    width: auto;\n    background-color: var(--quaternary-color);\n    overflow: hidden;\n    margin: 0;\n    padding: 0;\n    display: grid;\n    grid-template: max-content 1fr max-content / 1fr;\n    grid-template-areas:\n        \"header\"\n        \"content\"\n        \"footer\";\n}\n\n* {\n    padding: 0;\n    margin: 0;\n    font-family: 'robotoregular', sans-serif;\n}\n\n.web-info {\n    display: flex;\n    align-items: center;\n    cursor: pointer;\n}\n\n.web-logo {\n    height: 48px;\n    width: 48px;\n    position: relative;\n    top: -4px;\n}\n\n.web-title {\n    font-family: 'alex_brushregular', sans-serif;\n    color: var(--logo-color);\n    font-size: 48px;\n}\n\nheader {\n    grid-area: header;\n    background-image: linear-gradient(var(--main-color), var(--secondary-color));\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    padding: var(--pad-height) var(--pad-width);\n    box-shadow: var(--mild-shadow);\n}\n\n.tabs {\n    align-self: flex-end;\n    position: relative;\n    bottom: var(--pad-neg-height);\n    display: flex;\n    gap: var(--pad-width);\n}\n\n.tab {\n    font-family: 'robotothin', sans-serif;\n    font-size: 24px;\n    border: 0;\n    background-color: transparent;\n    padding-bottom: var(--pad-height);\n    color: var(--quaternary-color);\n    margin-bottom: 2px;\n}\n\n.tab:not(.active-tab):hover,\n.active-tab {\n    color: var(--logo-color);\n    border-bottom: 1px solid var(--logo-color);\n    margin-bottom: 1px;\n    text-shadow: var(--mild-shadow);\n} \n\n.active-tab {\n    cursor: auto;\n}\n\nbutton {\n    cursor: pointer;\n    font-family: 'robotobold', sans-serif;\n}\n\nbutton:not(.tab):hover {\n    box-shadow: var(--mild-shadow);\n}\n\nbutton.new-project {\n    border: 0;\n    padding: var(--pad-height) var(--pad-width);\n    border-radius: 25px;\n    font-family: 'robotobold', sans-serif;\n    background-color: var(--secondary-color);\n    color: var(--quaternary-color);\n}\n\nbutton.new-project:hover {\n    background-color: var(--logo-color);\n    text-shadow: var(--mild-shadow);\n    color: var(--secondary-color);\n}\n\ndiv#content {\n    grid-area: content;\n}\n\nfooter {\n    grid-area: footer;\n}\n\ndialog {\n    margin: auto;\n    padding: var(--pad-height) var(--pad-width);\n    overflow: visible;\n    border: 2px solid black;\n    border-radius: 5px;\n    background-color: var(--quaternary-color);\n}\n\ndialog::backdrop {\n    background-color: rgba(0, 0, 0, 0.10);\n}\n\nform {\n    display: flex;\n    flex-direction: column;\n    gap: var(--pad-height);\n}\n\n.cancel-btn {\n    position: absolute;\n    --square-pad: 5px;\n    --neg-square-pad: -8px;\n    top: var(--neg-square-pad);\n    right: var(--neg-square-pad);\n    padding: 0 var(--square-pad);\n    border-radius: 10px;\n    background-color: hsl(0, 50%, 50%);\n    border: 2px solid black;\n}\n\ninput,\nselect {\n    font-size: 14px;\n}\n\ninput[type=\"text\"] {\n    border: 0;\n    background-color: var(--quaternary-color);\n    border-bottom: 1px solid black;\n}\n\ninput {\n    outline: 0;\n}\n\n.group {\n    display: flex;\n    justify-content: flex-end;\n    gap: 8px;\n}\n\nlabel,\n.form-title {\n    font-family: 'robotobold';\n}\n\nlabel {\n    font-size: 18px;\n}\n\n.group > :not(label) {\n    width: 50%;\n}\n\ndialog .add-button:hover,\ndialog .cancel-btn:hover {\n    box-shadow: none;\n}\n\n#content {\n    display: grid;\n    grid-auto-rows: minmax(50px, 250px);\n    grid-template-columns: repeat(auto-fill, minmax(50px, 250px));\n    gap: 16px;\n    padding: 16px;\n    overflow: auto;\n}\n\n.project-card button {\n    height: 20px;\n    width: 20px;\n    border: 0;\n    background-size: 20px;\n    background-color: transparent;\n}\n\n.card-lower button:hover {\n    background-color: var(--light-tertiary-color);\n    border-radius: 5px;\n    overflow: visible;\n}\n\n.project-card {\n    border-radius: 15px;\n    background-color: var(--tertiary-color);\n    color: var(--quaternary-color);\n    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.15);\n    overflow: auto;\n    display: flex;\n    flex-direction: column;\n}\n\n.project-card:hover {\n    box-shadow: var(--mild-shadow);\n}\n\n.view {\n    background-image: url(\"../images/view-project.png\");\n}\n\n.project-add-task {\n    background-image: url(\"../images/add.png\");\n}\n\n.project-notes {\n    background-image: url(\"../images/notes.png\");\n}\n\n.card-upper {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    padding: var(--pad-height);\n    background-color: var(--secondary-color);\n    border-radius: 15px;\n}\n\n.card-upper:hover,\n.card-lower:hover {\n    padding-bottom: var(--pad-height);\n}\n\n.card-upper:hover {\n    border-bottom: 5px solid var(--main-color);\n}\n\n.card-lower:hover {\n    border-bottom: 5px solid var(--dark-tertiary-color);\n}\n\n.card-lower {\n    display: flex;\n    flex-direction: column;\n    padding: var(--pad-height);\n    flex-grow: 1;\n    gap: 18px;\n    border-radius: 15px;\n    justify-content: space-between;\n}\n\n.card-upper,\n.card-lower {\n    padding-bottom: calc(var(--pad-height) + 5px);\n}\n\n.card-buttons {\n    align-self: flex-end;\n    display: flex;\n    gap: 4px;\n}\n\n.project-desc {\n    hyphens: auto;\n}\n\n.card-upper .delete {\n    background-image: url(\"../images/delete.png\");\n    background-color: rgba(255, 68, 68, 0.5);\n    border-radius: 5px;\n}\n\n.card-upper .delete:hover {\n    background-color: rgb(255, 68, 68);\n}\n\n.bool-dialog {\n    padding: var(--generic-padding);\n    background-color: var(--quaternary-color);\n    display: flex;\n    flex-direction: column;\n    gap: 8px;\n}\n\n.question {\n    font-family: 'robotothin', sans-serif;\n    font-weight: bold;\n}\n\n.choices {\n    display: flex;\n    justify-content: space-around;\n}\n\n.dialog-btn {\n    padding: var(--small-padding);\n    border: 1px solid black;\n    border-radius: 5px;\n}\n\n.dialog-btn:hover {\n    box-shadow: var(--softer-shadow);\n}\n\n.yes-btn {\n    background-color: hsl(120, 100%, 50%);\n}\n\n.yes-btn:hover {\n    background-color: hsl(120, 100%, 75%);\n}\n\n.no-btn {\n    background-color: hsl(0, 50%, 50%);\n}\n\n.no-btn:hover {\n    background-color: hsl(0, 50%, 75%);\n}\n\n.task-list {\n    list-style-type: none;\n    margin: 0;\n    padding: 0;\n    display: flex;\n    flex-direction: column;\n    gap: 4px;\n}\n\n.task-container,\n.add-task-container {\n    display: flex;\n    gap: 8px;\n    font-family: 'robotothin', sans-serif;\n    align-items: center;\n    justify-content: space-between;\n}\n\n.tasks-header {\n    text-align: center;\n    border-bottom: 2px solid var(--main-color);\n    margin-bottom: 4px;\n}\n\n.task-btn {\n    --size: 20px;\n    background-image: url(\"../images/add.png\");\n    height: var(--size);\n    width: var(--size);\n    background-size: var(--size);\n    border: 0;\n    background-color: transparent;\n    position: relative;\n    right: 4px;\n}\n\nbutton.tasks-add-task.task-btn:hover {\n    box-shadow: none;\n}\n\n.tasks-add-task {\n    cursor: pointer;\n}\n\n.add-task-container {\n    gap: 0;\n    justify-content: flex-start;\n}\n\n.priority {\n    --size: 5px;\n    border-radius: var(--size);\n    height: var(--size);\n    width: var(--size);\n}\n\n.low-priority {\n    background-color: green;\n}\n\n.medium-priority {\n    background-color: yellow;\n}\n\n.high-priority {\n    background-color: red;\n}\n\n.due-date-task {\n    font-size: 0.75rem;\n    color: grey;\n}\n\n.new-task-container {\n    display: flex;\n    flex-direction: column;\n    gap: 8px;\n}\n\n.input-container > :nth-child(2) {\n    width: 50%;\n}\n\n.input-container {\n    display: flex;\n    justify-content: flex-end;\n    align-items: center;\n    gap: 4px;\n}\n\n.radios-container {\n    display: flex;\n    flex-direction: column;\n}\n\n.radio-container {\n    display: flex;\n}\n\n.task-submit {\n    align-self: flex-end;\n}\n\n.new-task-container .task-submit:hover {\n    box-shadow: none;\n}\n\n.delete-task {\n    --size: 15px;\n    background-image: url(\"../images/delete.png\");\n    background-size: var(--size);\n    height: var(--size);\n    width: var(--size);\n    border: 0;\n}\n\nbutton.delete-task:hover {\n    box-shadow: none;\n}\n\nfieldset {\n    border: 0;\n}\n\n.notes-container {\n    display: flex;\n    flex-direction: column;\n    gap: 8px;\n}\n\n.save-notes-btn {\n    align-self: flex-end;\n}\n\nbutton.save-notes-btn:hover {\n    box-shadow: none;\n}\n\n.save-notes-btn,\n.add-button,\nbutton.add-task {\n    border-radius: 5px;\n    align-self: flex-end;\n    padding: 8px 16px;\n    background-color: hsl(120, 100%, 50%);\n}\n\n\n.save-notes-btn:hover,\n.add-button:hover,\nbutton.add-task:hover {\n    background-color: hsl(120, 100%, 75%);\n}\n\nfooter {\n    display: flex;\n    justify-content: space-around;\n    padding: var(--generic-padding);\n    background-image: linear-gradient(var(--secondary-color), var(--main-color));\n    box-shadow: 0 -5px 10px rgba(0, 0, 0, 0.25);\n}\n\nfooter * {\n    color: var(--quaternary-color);\n}\n\na:hover {\n    text-decoration: none;\n}\n\n.project-started {\n    display: block;\n    font-family: 'robotobold', sans-serif;\n    color: var(--dark-tertiary-color);\n    text-align: end;\n}"],"sourceRoot":""}]);
